// fix for android kitkat 

// $border-thin: 1px;
// $border-thick: 3px;
$border-thin: .07rem;
$border-mid: .14rem;
$border-thick: .2rem;
html {
  font-size: 4.4vw;
}
body {
  font-size: 16px;
}

// _common
.scr-prod {
  border-width: $border-thin;
}
.box-table .tb-bd {
  border-width: $border-thin;
}
.btns {
  .type01,
  .type02,
  .type03 {
    border-width: $border-thin;
  }
}
.my-order-result ul .bundle {
  li {
    .set-prod {
      border-width: $border-thin;
    }
    .btm {
      a {
        border-width: $border-thin;
      }
    }
    .reason {
      border-width: $border-thin;
    }
    .request-txt textarea {
      border-width: $border-thin;
    }
    .prod-add .box .conts {
      border-width: $border-thin;
    }
  }
}
.scr-navi .sub-line {
  border-width: $border-thick;
}
.scr-prod .txt-box .brand {
  border-width: $border-thick;
}
.list2-prod2 > ul li .txt-box .brand {
  border-width: $border-thick;
}
.prod-view-top {
  border-width: $border-thick;
  .btns {
    min-width: 50%;
    text-align: right;
    &.left {
      text-align: left;
    }
  }
}
.cs-depth2 ul {
  border-width: $border-thick;
}


// _layout
.site-map {
  border-width: $border-thin;
}
footer .footer {
  border-width: $border-thin;
  .f-wrap .btn-customer-cnt {
    border-width: $border-thin;
  }
}
.kolon-wrap.main .kolon-header .h-wrap ul {
  border-width: $border-thick;
}


// AfterSales
.as-txt dd {
  border-width: $border-thin;
}
.as-inquiry form {
  input {
    border-width: $border-thin;
  }
  .as-btn {
    border-width: $border-thin;
  }
}
.as-pop .pop-result {
  border-width: $border-thick;
  li {
    border-width: $border-thin;
  }
  & + a {
    border-width: $border-thin;
  }
}


// Cart
.cart .cont {
  .c-list {
    .setItem {
      .set-option-list {
        border-width: $border-thin;
      }
    }
    .info .prod-add {
      .sizeSelect,
      button {
        border-width: $border-thin;
      }
      .conts {
        border-width: $border-thin;
      }
      .select {
        border-width: $border-thin;
      }
    }
    .btn-area {
      .btn1 {
        border-width: $border-thin;
      }
      .btn {
        border-width: $border-thin;
      }
    }
  }
  .btns {
    .click-del {
      border-width: $border-thin;
    }
    .click-cart {
      border-width: $border-thin;
    }
  }
}
.total-price {
  border-width: $border-thin;
}
.cart .top {
  border-width: $border-thick;
}


// Contact
.add-form form {
  ul li {
    border-width: $border-thin;
    .slt-prdt {
      border-width: $border-thin;
    }
    .slt-prdt-box {
      border-width: $border-thin;
    }

  }
  dl {
    border-width: $border-thick;
    dt {
      input {
        border-width: $border-thin;
      }
    }
    dd {
      border-width: $border-thin;
    }
    dd.sns-chk .check input+label {
      border-width: $border-thin;
    }
  }
}
.search-prod .prod-list li {
  border-width: $border-thin;
  .info .select {
    border-width: $border-thin;
  }
}
.btn-contact {
  border-width: $border-thin;
}
.select-period ul {
  border-width: $border-thin;
  li a {
    border-width: $border-thin;
  }
}


// Coupon, Deposit
.offline-coupon-shop .scroll-area .select-shop {
  select,
  input[type="text"] {
    border-width: $border-thin;
  }
  .btn {
    border-width: $border-thin;
  }
}


// eKOLON
.cnt-box {
  border-width: $border-thin;
}


// Event
.top-sub-tit {
  border-width: $border-thick;
}


// GuestOrder
.wrap-form-login .form-login .in-txt {
  input {
    border-width: $border-thin;
    &.active {
      border-width: $border-thin;
    }
  }
}
.order .d-info {
  .add .btn-area .close {
    border-width: $border-thin;
  }
  li {
    border-width: $border-thin;
    .cont .btn {
      border-width: $border-thin;
    }
    &.request-box {
      .request-txt {
        border-width: $border-thin;
      }
    }
  }
}
.complete .box2 {
  border-width: $border-thick;
  .dl-table {
    border-width: $border-thin;
    dt {
      border-width: $border-thin;
    }
  }
  .btns a {
    border-width: $border-thin;
  }
}
.mypage-order {
  .my-order-check {
    select {
      border-width: $border-thin;
    }
    input {
      border-width: $border-thin;
    }
  }
  .my-order-result {
    ul {
      .bundle li {
        .set-prod {
          border-width: $border-thin;
        }
        .btm p {
          span + span {
            border-width: $border-thin;
          }
        }
        .request-txt textarea {
          border-width: $border-thin;
        }
        .prod-add .box .conts {
          border-width: $border-thin;
        }
      }
    }
  }
  .btn-back a {
    border-width: $border-thin;
  }
  .order {
    &.cancel-refund {
      h3 {
        border-width: $border-thick;
      }
      .dl-table {
        dt {
          border-width: $border-thin;
        }
        dd {
          border-width: $border-thin;
        }
      }
    }
    &.cancel-return {
      .d-top {
        border-width: $border-thick;
      }
    }
    .d-info .add ul li:last-child {
      border-width: $border-thin;
    }
  }
  .cancel-btns .btn1 {
    border-width: $border-thin;
  }
  .cancel-end .btn a {
    border-width: $border-thin;
  }
  .confirm-btn a {
    border-width: $border-thin;
  }
  .cancel-exchange > div {
    .bd {
      border-width: $border-thick;
    }
  }
  .cancel-gift + .complete + .complete {
    .box2 h3 {
      border-width: $border-thick;
    }
  }
  .return-notice h3 {
    border-width: $border-thick;
  }
}


// Login
.wrap-form-login .form-login {
  .in-txt input {
    border-width: $border-thin;
    &.active {
      border-width: $border-thin;
    }
  }
  .btns .btn-nonmbr {
    border-width: $border-thin;
  }
}


// Main
#kolon-content.setting {
  .user .btn-login {
    border-width: $border-thin;
  }
  .login {
    border-width: $border-thin;
  }
  .notice {
    .box-txt {
      border-width: $border-thin;
    }
    .box-btn {
      .type01 {
        border-width: $border-thin;
      }
    }
  }
}
.kolon-menu {
  nav {
    .main-menu li {
      border-width: $border-thin;
      &.depth {
        .depth-02 {
          border-width: $border-thin;
        }
        .depth-03 {
          border-width: $border-thin;
        }
      }
    }
    .sub-menu .member .setting {
      border-width: $border-thin;
    }
    .brand-list .list {
      border-width: $border-thin;
    }
  }
  .gnb .my-info .user-log {
    strong {
      border-width: $border-thin;
    }
    u {
      border-width: $border-thin;
    }
    .separater {
      border-width: $border-thin;
    }
  }
}
.list1-prod > ul {
  li .txt-box .brand {
    border-width: $border-thick;
  }
}
.main .scr-navi {
  border-width: $border-thin;
}

// MyPage
.wow-my-kolon ul li {
  border-width: $border-thin;
}
.family-guide .box2 dl {
  border-width: $border-thick;
  dt {
    border-width: $border-thin;
  }
  dd {
    border-width: $border-thin;
  }
}


// Notice
.cs-faq .toggle-list li {
  border-width: $border-thin;
}
.cs-faq .point-sort::after {
  border-width: $border-thick;
}


// Order
.order {
  .order-prod {
    .top {
      border-width: $border-thick;
    }
    .setItem .set-option-list {
      border-width: $border-thin;
    }
  }
  .gift .top {
    border-width: $border-thin;
  }
  .ad-info .d-cont {
    border-width: $border-thin;
    .address .d-txt {
      border-width: $border-thin;
    }
  }
  .d-discount {
    .tg-cont {
      .input {
        border-width: $border-thin;
      }
      .btn {
        border-width: $border-thin;
      }
    }
    .coupon .tg-cont {
      li {
        border-width: $border-thin;
      }
      .add input {
        border-width: $border-thin;
      }
      .add-coupon {
        border-width: $border-thin;
      }
    }
    .staff .tg-cont {
      div {
        border-width: $border-thin;
      }
    }
    .deposit .tg-cont {
      input {
        border-width: $border-thin;
      }
    }
  }
  .payment + .payment {
    border-width: $border-thin;
  }
  .payment .cont {
    .pay-tab {
      li {
        border-width: $border-thin;
      }
      .on {
        border-width: $border-thin;
      }
    }
    .deposit {
      .txt {
        border-width: $border-thin;
      }
      .add {
        select {
          border-width: $border-thin;
        }
        input {
          border-width: $border-thin;
        }
      }
    }
    .agree {
      border-width: $border-thin;
    }
  }
  .d-top {
    border-width: $border-thick;
  }
}
.list2-prod2 > ul {
  border-width: $border-thin;
}
.order-notice .box1 {
  border-width: $border-thin;
}
.only-employee {
  .add .input {
    input[type="text"] {
      border-width: $border-thin;
    }
    .btn {
      border-width: $border-thin;
    }
  }
  .wrap-c-list .list {
    border-width: $border-thin;
  }
  .btn-area .cencel {
    border-width: $border-thin;
  }
}
.wrap-pickup-ticket .wrap-ticket-list .ticket-list {
  .pickup-product .set-prod {
    border-width: $border-thin;
  }
  .pickup-ticket {
    border-width: $border-thin;
    .btn-review a {
      border-width: $border-thin;
    }
  }
}
.ticket-info-area {
  .sns-service {
    border-width: $border-thin;
  }
  .btn-area a {
    &.cancel {
      border-width: $border-thin;
    }
    &.confirm {
      border-width: $border-thin;
    }
  }
}


// Point
.dpst-list .detail {
  border-width: $border-thin;
}


// Product
.prod-detail-main .detail-info .info-box .brand i {
  border-width: $border-thin;
}
.prod-detail-contents {
  .detail-review .review-list .wrap-list .list .my-review a {
    border-width: $border-thin;
  }
  .detail-special {
    border-width: $border-thick;
  }
  .prod-list {
    .list-wrap {
      .swiper-slide {
        a {
          width: 30.333%;
          margin: 0 1.5% 3%;
        }
      }
    }
  }
}
.detail-btn-area {
  .btn-area,
  .type01,
  .type02 {
    border-width: $border-thin;
  }
}


// Proposal
.box-form-pral .form-pral {
  .ch-area {
    border-width: $border-thin;
  }
  input {
    border-width: $border-thin;
  }
}


// Review
.box-list .list-review .prdt .btn-wrt a {
  border-width: $border-thin;
}
.box-write {
  .form-review {
    input,
    select,
    textarea {
      border-width: $border-thin;
    }
  }
  .btns {
    .click-cl {
      border-width: $border-thin;
    }
    .click-rgt {
      border-width: $border-thin;
    }
  }
}


// RMA
.order {
  .d-top {
    border-width: $border-thick;
  }
  .d-info .add {
    border-width: $border-thick;
  }
  .d-info li {
    border-width: $border-thin;
    .cont .btn {
      border-width: $border-thin;
    }
  }
  .gift .top {
    border-width: $border-thin;
  }
}
.mypage-order {
  .my-order-check {
    .btn-inquiry a {
      border-width: $border-thin;
    }
    select {
      border-width: $border-thin;
    }
    .add-prod input {
      border-width: $border-thin;
    }
  }
  .btn-back a {
    border-width: $border-thin;
  }
  .order-number + .top {
    border-width: $border-thick;
  }
}


// Search
.typing ul li {
  border-width: $border-thin;
}
.hs-border {
  border-width: $border-thick;
}



// MyAddress
.detail-address {
  .box-address {
    .input-addr {
      input {
        border-width: $border-thin;
      }
    }
  }
}
.my-address {
  .top .radio input:checked + label {
    background-size: contain;
  }
  .ad-list {
    dl {
      border-width: $border-thin;
    }
    .edit {
      border-width: $border-thin;
    }
  }
}
.d-info {
  .add .btn-area .close {
    border-width: $border-thin;
  }
  li {
    border-width: $border-thin;
    .cont .btn {
      border-width: $border-thin;
    }
    &.request-box .request-txt {
      border-width: $border-thin;
    }
  }
}
.search-zipcode .search-top .search-wrap {
  border-width: $border-mid;
}
.bt-minus {
  background: url('/_ui/img/m/icn/icn-minus4.png') no-repeat center;
  background-size: 2.933vw;
}

.kolon-menu nav .brand-list {
  .brand-open {
    border-width: $border-thin;
  }
  .tabs {
    border-width: $border-thin;
  }
}
